.no-focus *:focus {
  outline: 0; }

.responsive-embed {
  position: relative;
  width: 100%;
  height: 0;
  padding: 56.25% 0 0 0;
  display: block; }
  .responsive-embed iframe {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%; }

.wrapper {
  width: 100%;
  height: 100%;
  border-left: 10% solid #ffffff;
  margin-top: 30px; }
  .wrapper:before {
    content: "";
    position: absolute;
    background: #ffffff;
    left: 0;
    right: 0;
    height: 100%;
    width: 10%; }

.navigation {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start; }
  .navigation__item, .navigation__link {
    font-size: 1.6rem;
    line-height: 2.2rem;
    font-family: "Cantarell", sans-serif;
    font-weight: 400;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }
    @media screen and (min-width: 992px) {
      .navigation__item, .navigation__link {
        font-size: 1.6rem;
        line-height: 2.2rem; } }
    @media print {
      .navigation__item, .navigation__link {
        font-size: 9pt;
        line-height: 11pt; } }
  .navigation__item {
    background-color: transparent;
    border: none;
    cursor: pointer;
    margin-right: 30px;
    letter-spacing: 0.06rem; }
    .navigation__item a {
      color: #000000;
      text-decoration: none; }
    .navigation__item:hover .navigation__link:after,
    .navigation__item:hover .navigation__text:after, .navigation__item:focus .navigation__link:after,
    .navigation__item:focus .navigation__text:after, .navigation__item:active .navigation__link:after,
    .navigation__item:active .navigation__text:after,
    .navigation__item .active .navigation__link:after,
    .navigation__item .active .navigation__text:after {
      animation-name: animate-menu-dotty;
      animation-duration: .5s;
      animation-iteration-count: infinite;
      animation-timing-function: linear; }
    .navigation__item:first-child {
      padding-left: 0;
      margin-right: 0px; }
  .navigation__link:after, .navigation__text:after {
    content: '';
    width: 97%;
    height: 1px;
    display: block;
    background-position-x: 0;
    position: absolute;
    background-repeat: repeat-x;
    bottom: -1px;
    left: 3%;
    background-image: url(../svg/menu-dotty.min.svg); }

.anchor {
  cursor: pointer;
  color: #000000;
  text-decoration: none;
  letter-spacing: 0.06rem; }
  .anchor__link:after, .anchor__text:after {
    content: '';
    width: 99%;
    height: 1px;
    display: block;
    background-position-x: 0;
    position: absolute;
    background-repeat: repeat-x;
    bottom: -1px;
    left: 0.5%;
    background-image: url(../svg/menu-dotty.min.svg); }
  .anchor:hover .anchor__link:after,
  .anchor:hover .anchor__text:after, .anchor:focus .anchor__link:after,
  .anchor:focus .anchor__text:after, .anchor:active .anchor__link:after,
  .anchor:active .anchor__text:after,
  .anchor .active .anchor__link:after,
  .anchor .active .anchor__text:after {
    animation-name: animate-menu-dotty;
    animation-duration: .5s;
    animation-iteration-count: infinite;
    animation-timing-function: linear; }

.text {
  margin-top: 80px;
  margin-bottom: 50px; }
  .title-h2 + .text {
    margin-top: 30px; }
  .text__item {
    margin-bottom: 20px;
    letter-spacing: 0.06rem; }
  @media (max-width: 479px) {
    .text {
      margin-top: 30px; } }
  .text__item--first {
    margin-bottom: 0; }
  .text__item--underlien .text__txt:after {
    content: '';
    width: 99%;
    height: 1px;
    display: block;
    background-position-x: 0;
    position: absolute;
    background-repeat: repeat-x;
    bottom: -1px;
